import { BaseInputProperties } from '@umetrics/sartorius-ui-base-input';

export enum HeaderSize {
    Medium = 'medium',
    Large = 'large',
}

const mapHeaderSizeToProps: Record<HeaderSize, string> = {
    medium: '16px Roboto',
    large: '18px Roboto',
};

export const getHeaderProperties = (headerSize: HeaderSize): string => mapHeaderSizeToProps[headerSize];

export interface InlineEditProperties extends BaseInputProperties {
    placeholder?: string;
    error?: boolean;
    saveTooltipText?: string;
    cancelTooltipText?: string;
    inputTooltipText?: string;
    disabledButton?: boolean;
    maxLength?: number;

    focus?: (options?: FocusOptions) => Promise<void>;
    blur?: () => Promise<void>;
    setSelectionRange?: (
        start: number | null,
        end: number | null,
        direction?: 'forward' | 'backward' | 'none'
    ) => Promise<void>;

    header?: boolean;
    headerSize?: HeaderSize | string;
    boldText?: boolean;

    onInlineEditToggle?: any;
    onInlineEditSave?: any;
    onInlineEditClose?: any;
}
