import {
    booleanConverter,
    createClassName,
    defineElement,
    html,
    ifDefined,
    property,
    query,
} from '@umetrics/sartorius-ui-base';
import { inlineEditStyle } from './inlineEditStyle';
import { getHeaderProperties, HeaderSize, InlineEditProperties } from './inlineEditProperties';
import ok from '@umetrics/sartorius-ui-icon/dist/icons/ok';
import cancel from '@umetrics/sartorius-ui-icon/dist/icons/cancel';
import { version } from './metadata';
import { BaseInput } from '@umetrics/sartorius-ui-base-input';
import { InlineEditEvent } from './inlineEditEvent';
import { perfDebounce } from '@umetrics/sartorius-ui-utils';

@defineElement({
    tagName: 'inline-edit',
    styles: inlineEditStyle,
    version,
})
export class InlineEdit extends BaseInput<InlineEdit> implements InlineEditProperties {
    // Static members
    private static TooltipService;
    private static TooltipServiceDelay;
    private static Button;
    private static Icon;

    // Public members
    @property({ type: String, reflect: true })
    value;
    @property({ type: String, reflect: false })
    placeholder;
    @property({ type: Boolean, converter: booleanConverter })
    error;
    @property({ type: String, reflect: false, attribute: 'save-tooltip-text' })
    saveTooltipText;
    @property({ type: String, reflect: false, attribute: 'cancel-tooltip-text' })
    cancelTooltipText;
    @property({ type: String, reflect: false, attribute: 'input-tooltip-text' })
    inputTooltipText;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'disabled-button' })
    disabledButton;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    focused;
    @property({ type: Boolean, converter: booleanConverter, reflect: false })
    header;
    @property({ type: HeaderSize, reflect: false, attribute: 'header-size' })
    headerSize;

    @property({ type: Boolean, converter: booleanConverter, reflect: false, attribute: 'bold-text' })
    boldText;
    @property({ type: Number, reflect: false, attribute: 'max-length' })
    maxLength;

    @query('input')
    public input!: HTMLInputElement;
    private _showTitle = false;

    // Private members
    private _editingValue = '';
    private _debouncedCallback;
    private _resizeObserver;

    async focus(options?: FocusOptions): Promise<void> {
        await this.updateComplete;
        this.input.focus(options);
    }

    async blur(): Promise<void> {
        await this.updateComplete;
        this.input.blur();
    }

    setSelectionRange = async (
        start: number | null,
        end: number | null,
        direction?: 'forward' | 'backward' | 'none'
    ): Promise<void> => {
        await this.updateComplete;
        this.input.setSelectionRange(start, end, direction);
    };

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({
            module: () => import('@umetrics/sartorius-ui-tooltip-service'),
            elem: ['TooltipService', 'TooltipServiceDelay'],
        });
        imports.push({ module: () => import('@umetrics/sartorius-ui-button'), elem: ['Button'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });

        InlineEdit.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
        // Start with auto will cause resizeobserver to make it grow cyclic until settled.
        this.style.setProperty('width', '100%');
    }

    disconnectedCallback() {
        super.disconnectedCallback();
        this._uninstallResizeObserver(this);
    }

    protected firstUpdated(changes): void {
        this._measureAndSetWidth();
        this._editingValue = this.value;
        this.addEventListener('focus', this._onFocus);
        this.addEventListener('blur', this._handleBlur);
        this.addEventListener('keydown', this._handleKeydown);

        requestIdleCallback(() => {
            this.style.removeProperty('width');
            this._installResizeObserver(this);
        });
        super.firstUpdated(changes);
    }

    protected update(changes): void {
        if (changes.has('value')) {
            this._measureAndSetWidth();
            this._editingValue = this.value;
        }
        super.update(changes);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            focused: this.focused,
            disabled: this.disabled,
            error: this.error,
            header: this.header,
            [this.headerSize ?? HeaderSize.Medium]: true,
            boldText: this.boldText,
        });

        const saveTooltipHtml = this.saveTooltipText
            ? html`
                <${InlineEdit.TooltipService.tag} delay="${InlineEdit.TooltipServiceDelay.Short}"
                                                  object-id="save-button-tooltip" location="top-right">
                    <span>${this.saveTooltipText}</span></${InlineEdit.TooltipService.tag}>`
            : null;
        const cancelTooltipHtml = this.cancelTooltipText
            ? html`
                <${InlineEdit.TooltipService.tag} delay="${InlineEdit.TooltipServiceDelay.Short}"
                                                  object-id="cancel-button-tooltip" location="top-right">
                    <span>${this.cancelTooltipText}</span></${InlineEdit.TooltipService.tag}>`
            : null;

        const inputTooltipHtml = this.inputTooltipText
            ? html`
                <${InlineEdit.TooltipService.tag} show="${!!this.focused}"
                                                  delay="${InlineEdit.TooltipServiceDelay.Short}" object-id="input"
                                                  location="top-right"><span>${
                                                      this.inputTooltipText
                                                  }</span></${InlineEdit.TooltipService.tag}>`
            : null;

        const buttonHtml = this.focused
            ? html`
                <${InlineEdit.Button.tag} class="${baseClassName}-save" @click=${
                    this._saveChangesButtonClick
                } button-size="inline-small" disabled=${
                    this.value === this._editingValue || !!this.disabledButton
                } id="save-button-tooltip" compact="true" type="tertiary">
                    <${InlineEdit.Icon.tag} slot="icon-slot" icon="${ok}"></${InlineEdit.Icon.tag}>
                </${InlineEdit.Button.tag}>
                <${InlineEdit.Button.tag} class="${baseClassName}-cancel" @mousedown=${
                    this._cancelChangesButtonClick
                } button-size="inline-small" id="cancel-button-tooltip" compact="true" type="tertiary">
                    <${InlineEdit.Icon.tag} slot="icon-slot" icon="${cancel}"></${InlineEdit.Icon.tag}>
                </${InlineEdit.Button.tag}>
                ${saveTooltipHtml}
                ${cancelTooltipHtml}
            `
            : html``;

        return html`
            <div class="${rootClasses}" title=${ifDefined(!this.focused && this._showTitle ? this.value : undefined)}>
                <input
                    data-testid="inline-edit-input"
                    id="input"
                    class="${baseClassName}-input"
                    .value="${this.value}"
                    placeholder=${ifDefined(this.placeholder ? this.placeholder : undefined)}
                    maxlength="${ifDefined(this.maxLength > 0 ? this.maxLength : undefined)}"
                    ?disabled="${!!this.disabled}"
                    type="text"
                    tabindex="0"
                    @click="${() => {
                        this.focused = true;
                        this.requestUpdate('focused');
                    }}"
                    @input="${this._startEditing}"
                />
                ${inputTooltipHtml} ${buttonHtml}
            </div>
        `;
    }

    // Blur on enter
    protected _handleKeydown(e: KeyboardEvent): void {
        const indexElement = e.composedPath().indexOf(this);

        if (indexElement === 3 && !this.disabledButton) {
            if (e.code === 'Enter') {
                if (this._editingValue === this.value) {
                    this._cancelChanges();
                } else {
                    this._saveChanges();
                }
                this.blur();
                this.setSelectionRange(0, 0);
            } else if (e.code === 'Escape') {
                this._cancelChanges();
                this.blur();
                this.setSelectionRange(0, 0);
            }
        }
    }

    private _measureAndSetWidth = (shift = 0) => {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');

        this._debouncedCallback = undefined;
        if (this.input && !this.focused) {
            if (ctx) {
                this.input.style.setProperty('--input-width', this._measeureContext(ctx, shift));
            }
        }
    };

    private _measureAndSetWidthDebounced(inShift = 0) {
        // Performance debouncer, do once when browser idle
        if (!this._debouncedCallback) {
            this._debouncedCallback = perfDebounce(this._measureAndSetWidth, 'idle');
        }
        this._debouncedCallback(inShift);
    }

    private _measeureContext(ctx, shift) {
        ctx.font = this.header ? getHeaderProperties(this.headerSize ?? HeaderSize.Medium) : '14px Roboto';
        const metrics = ctx.measureText(this.value ? this.value : this.placeholder);
        let width = metrics.width + 16 + 8; // 8 = padding right, 16 some buffer?
        const offsetWidth = this.input.parentElement!.offsetWidth;
        const prevShowTitle = this._showTitle;
        if (width > offsetWidth) {
            this._showTitle = true;
            width = offsetWidth;
        } else {
            this._showTitle = false;
        }

        if (!this.focused && prevShowTitle !== this._showTitle && offsetWidth > 0) {
            // Needed? Better make _showTitle a state?
            this.requestUpdate();
        }
        return width - shift + 'px';
    }

    private _cancelChangesButtonClick(e) {
        if (e.which <= 1) {
            // Lefty 1, others > 1, jsdom 0
            this._cancelChanges();
        }
    }

    private _saveChangesButtonClick(e) {
        if (!this.disabledButton && e.which <= 1) {
            // Lefty 1, others > 1, jsdom 0
            this._saveChanges();
        }
    }

    private _saveChanges() {
        // At this point on firefox, the toggle event is not dispatched due to no blur event received!?
        this.focused = false;

        if (this._editingValue !== this.value) {
            this.value = this._editingValue;
            this.input.value = this.value;
            this._dispatchEvent(InlineEditEvent.Save);
        }
    }

    private _cancelChanges(): void {
        this.focused = false;
        this.requestUpdate('focused');

        this.input.value = this.value;
        this.input.scrollLeft = 0;
    }

    private _startEditing(e) {
        this._editingValue = e.target.value;
        this.requestUpdate('_editingValue');
    }

    private _onFocus(): void {
        // Make space for the buttons that will appear on requestUpdate below

        this.input.style.width = this.input.style.width ? `calc(${this.input.style.width} - 51px)` : '';
        const handler = () => {
            this.focused = true;
            this.requestUpdate('focused');
            this._dispatchEvent(InlineEditEvent.Toggle);
            this.input.style.removeProperty('width');
        };
        window.requestAnimationFrame(handler.bind(this));
    }

    private _handleBlur(): void {
        const handler = () => {
            if (this.focused) {
                this.focused = false;
                this.requestUpdate('focused');
                if (this._editingValue !== this.value) {
                    this._saveChanges();
                } else {
                    this._dispatchEvent(InlineEditEvent.Close);
                    this._measureAndSetWidthDebounced();
                }
            } else {
                this._measureAndSetWidthDebounced();
            }
            this._dispatchEvent(InlineEditEvent.Toggle);
        };
        window.requestAnimationFrame(handler.bind(this));
    }

    private _dispatchEvent(event) {
        this.dispatchEvent(
            new CustomEvent(event, {
                detail: {
                    open: this.focused,
                    value: this.value,
                },
                bubbles: true,
                composed: true,
                cancelable: true,
            })
        );
    }

    private _installResizeObserver(elem) {
        this._resizeObserver = new ResizeObserver(() => {
            this._measureAndSetWidthDebounced();
        });
        this._resizeObserver.observe(elem);
    }

    private _uninstallResizeObserver(elem) {
        if (this._resizeObserver) {
            this._resizeObserver.unobserve(elem);
            this._resizeObserver.disconnect();
        }
    }
}
