type DebouncedFunction<T extends (...args: any) => any> = (...args: Parameters<T>) => ReturnType<T>;

// Polyfill for requestIdleCallback and cancelIdleCallback to make function compatible with Safari iOS.
export const requestIdleCallback =
    window.requestIdleCallback ||
    function (callback: Function) {
        return setTimeout(() => {
            callback({
                didTimeout: false,
                timeRemaining: () => Math.max(0, 50), // Simulates idle time remaining
            });
        }, 50);
    };

export const cancelIdleCallback = window.cancelIdleCallback || clearTimeout;

/**
 * Returns a debounced version of the provided function that delays its execution.
 * If a `limit` is provided, the function is debounced using a timeout with the specified delay (ms).
 * If the `limit` is set to `idle`, the function is debounced based on the browser's idle time using `requestIdleCallback`.
 *
 * @param func The function to debounce.
 * @param limit The delay in milliseconds. 'idle' and the function will be executed during idle time.
 *              Defaults to 50 milliseconds.
 * @return A debounced version of the original function, which delays execution according to the `limit` or idle time.
 *
 * Example usage:
 *
 * ```typescript
 *        private _debouncedCallback;
 *        if (!this._debouncedCallback) {
 *             this._debouncedCallback = perfDebounce((arg) => {
 *                 console.log('debounced', arg);
 *             }, 'idle');
 *         }
 *         this._debouncedCallback(arg);
 * ```
 */
export function perfDebounce<T extends (...args: any) => any>(
    func: T,
    limit: number | 'idle' = 50
): DebouncedFunction<T> {
    let timeout: any;
    let idleCallback: any;
    let lastResult: ReturnType<T>;

    return function (this: any): ReturnType<T> {
        // eslint-disable-next-line prefer-rest-params
        const args = arguments;
        const later = () => {
            timeout = null;
            lastResult = func(...args);
        };
        if (limit === 'idle') {
            if (idleCallback) {
                cancelIdleCallback(idleCallback);
            }
            // Schedule the work during idle time if limit is -1
            idleCallback = requestIdleCallback(() => {
                lastResult = func(...args);
            });
        } else {
            clearTimeout(timeout);
            timeout = setTimeout(later, limit);
        }
        return lastResult;
    };
}
