import { css, cssSelector, cssVariableAsVar, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const inlineEditStyle: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const grayFour = cssVariableAsVar('color_secondary_3');

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                max-width: 100%;
                width: auto;
            }

            ${root} {
                display: flex;
                width: 100%;
                align-items: center;
                justify-content: flex-start;
                height: 24px;
                gap: 1px;
            }

            ${child('input')} {
                background-color: var(--inline-editing-background-color, transparent);
                display: inline-block;
                color: var(--inline-editing-text-color, black);
                border: 1px solid transparent;
                padding-right: 8px;
                height: 24px;
                margin-right: 1px;
                caret-color: #3c3c3c;
                font-size: 14px;
                min-width: 50px;
                width: var(--input-width, 200px);
            }

            ${is('focused')} ${child('input')} {
                transition: width 0.2s;
                width: calc(100% - 51px);
            }

            :host([focused]) {
                width: 100%;
                flex-grow: 1;
            }

            ${is('error', 'focused')} ${child('input')} {
                border: 1px solid ${cssVariableAsVar('color_traffic_light_1')} !important;
            }

            ${is('!disabled', '!focused')} ${child('input')}:hover {
                border: 1px solid ${grayFour};
            }

            ${is('disabled')} ${child('input')} {
                color: var(--inline-editing-text-color, black);
                pointer-events: none;
            }

            ${is('header', 'medium')} ${child('input')} {
                font-size: 16px;
            }

            ${is('header', 'large')} ${child('input')} {
                font-size: 18px;
            }

            ${is('boldText')} ${child('input')} {
                font-weight: 500;
            }

            ${child('input')}:focus {
                outline: none;
            }

            ${child('input')}:focus-visible {
                outline: none;
            }

            ${is('focused')} ${child('input')} {
                border: var(--inline-edit-border-color, 1px solid #3c3c3c);
                caret-color: var(--inline-edit-caret-color, #3c3c3c);
            }

            ${is('!focused')} ${child('input')} {
                text-overflow: ellipsis;
                caret-color: var(--inline-edit-caret-color, transparent);
            }
        `,
    ];
};
